body {
  background: black;
  /* color: white; */
  padding: 0;
  margin: 0;
}

.App {
  font-family: sans-serif;
  padding: 20px;
}

.App h1 {
  color: yellow;
}

.star-logo {
  text-align: center;
}

table {
  border-collapse: collapse;
  color: yellow;
}

thead {
  color: white;
}

thead td {
  padding: 10px 0;
  cursor: pointer;
}

thead td i {
  padding-top: 10px;
  /* cursor: pointer; */
}

tbody td {
  padding: 5px;
}

tbody .total h1 {
  padding: 10px 5px;
  font-size: 20px;
  background-color: #f2f2f2;
  color: black;
  margin-top: 10px;
}

tbody tr:nth-child(even) {
  background-color: yellow;
  color: black;
}

tbody tr:nth-child(odd) {
  background-color: black;
}

.film-detail h1,
.film-detail p {
  color: yellow;
}

.film-detail .film-detail-select {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.film-detail .film-detail-select .film-detail-select__select {
  width: 200px;
}

.loading {
  padding: 30px;
  padding-top: 60px;
  color: yellow;
  text-align: center;
}

.marquee {
  overflow: hidden;
  height: 50px;
  overflow: hidden;
  position: relative;
  animation: slide 0.5 linear infinite;
}

.marquee span {
  white-space: nowrap;
  color: yellow;
  position: absolute;
  width: 100%;
  margin: 0;
  line-height: 50px;
  text-align: center;
  /* Starting position */
  -moz-transform: translateX(100%);
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  /* Apply animation to this element */
  -moz-animation: slide 15s linear infinite;
  -webkit-animation: slide 15s linear infinite;
  animation: slide 15s linear infinite;
}

@keyframes slide {
  0% {
    transform: translateX(100vw);
  }
  100% {
    transform: translateX(-400vw);
  }
}
